#header {
    width: 100vw;
    height: 100px;
    background: transparent;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    transition: .5s;
}

#header.hidden {
    height: 0px;
}

#header div {
    height: 50px;
    display: flex;
    cursor: pointer;
    position: absolute;
    align-items: center;
    justify-content: center;
}

#header #list {
    width: 50px;
    left: 20px;
}

#header #list img {
    width: 28px;
    height: 28px;
}

#header #logo img {
    width: 105px;
    height: 42px;
}

#header #post {
    width: 50px;
    right: 110px;
}

#header #post img {
    width: 28px;
    height: 28px;
}

#header #Login {
    width: 100px;
    text-align: center;
    right: 10px;
}

#header #Login span {
    font-size: 18px;
    font-weight: bold;
}

@media (max-width: 650px) {

    #header #post {
        right: 20px;
    }

    #header #Login {
        display: none;
    }
}