#loader {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    opacity: 1;
}

#loader.dilute {
    animation: dilute .5s ease-in forwards;
}

#loader .bg {
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: linear-gradient(200deg, #e3c5eb, #a9c1ed);
}

#loader .loader {
    position: relative;
    width: 70px;
    height: 70px;
}

#loader .loader-large {
    position: absolute;
    inset: 0;
    margin: auto;
    width: 100%;
    height: 100%;
    background-color: #D4ADFC;
    border-radius: 10px;
    animation: loading 2s infinite;
}

#loader .loader-small {
    position: absolute;
    width: 50%;
    height: 50%;
    inset: 0;
    margin: auto;
    background-color: #5C469C;
    z-index: 2;
    border-radius: 6px;
    animation: loading 2s infinite reverse;
}

#loader ul li {
    position: absolute;
    border: 1px solid #fff;
    background-color: #fff;
    width: 30px;
    height: 30px;
    list-style: none;
    opacity: 0;
}

.square li {
    top: 40vh;
    left: 60vw;
    /* 执行动画：动画名 时长 线性的 无限次播放 */
    animation: square 10s linear infinite;
}

.square li:nth-child(2) {
    top: 80vh;
    left: 10vw;
    /* 设置动画延迟时间 */
    animation-delay: 2s;
}

.square li:nth-child(3) {
    top: 80vh;
    left: 85vw;
    /* 设置动画延迟时间 */
    animation-delay: 4s;
}

.square li:nth-child(4) {
    top: 10vh;
    left: 70vw;
    /* 设置动画延迟时间 */
    animation-delay: 6s;
}

.square li:nth-child(5) {
    top: 10vh;
    left: 10vw;
    /* 设置动画延迟时间 */
    animation-delay: 8s;
}

.circle li {
    bottom: 0;
    left: 15vw;
    /* 执行动画 */
    animation: circle 10s linear infinite;
}

.circle li:nth-child(2) {
    left: 35vw;
    /* 设置动画延迟时间 */
    animation-delay: 2s;
}

.circle li:nth-child(3) {
    left: 55vw;
    /* 设置动画延迟时间 */
    animation-delay: 6s;
}

.circle li:nth-child(4) {
    left: 75vw;
    /* 设置动画延迟时间 */
    animation-delay: 4s;
}

.circle li:nth-child(5) {
    left: 90vw;
    /* 设置动画延迟时间 */
    animation-delay: 8s;
}

#loader span {
    font-size: 20px;
    font-family: sans-serif;
    position: absolute;
    color: #5C469C;
}

#loader .logo-text {
    top: calc(50% - 75px);
    font-weight: bold;
}

#loader .value-text {
    top: calc(50% + 50px);
}

/* 動畫 Animation */

@keyframes loading {
    0% {
        transform: rotate(0deg);
    }

    50% {
        transform: rotateY(180deg);
    }

    100% {
        transform: rotateX(180deg);
    }
}

@keyframes square {
    0% {
        transform: scale(0) rotateY(0deg);
        opacity: 1;
    }

    100% {
        transform: scale(5) rotateY(1000deg);
        opacity: 0;
    }
}

@keyframes circle {
    0% {
        transform: scale(0) rotateY(0deg);
        opacity: 1;
        bottom: 0;
        border-radius: 0;
    }

    100% {
        transform: scale(5) rotateY(1000deg);
        opacity: 0;
        bottom: 90vh;
        border-radius: 50%;
    }
}

@keyframes dilute {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}